





























































































































export default {
	data() {
		return {
		w:{}	
		};
	},
	onLoad() {
		this.getw();
	},
	methods:{
		getw(){
			uni.request({
				url: 'https://api.thinker.ink/v1/weather/?city=武汉',
				method: 'GET',
				data: {},
				success: res => {
					console.log(res.data.data);
					this.w = res.data.data;
					
				},
				fail: () => {},
				complete: () => {}
			});
		}
	}
	
}
